body::before,
body::after,
.circle-blue-1,
.circle-blue-2 {
    content: "";
    position: absolute;
    border-radius: 50%;
    z-index: -1;
}

body::before {
    width: 90px;
    height: 90px;
    top: 140px;
    right: 130px;
    background-color: rgba(255, 235, 205, 0.5);
    /* Existing beige color */
}

body::after {
    width: 200px;
    height: 200px;
    bottom: -100px;
    left: -50px;
    background-color: rgba(255, 235, 205, 0.5);
    /* Existing beige color */
}

/* Additional Blue Circles */
.circle-blue-1 {
    width: 50px;
    height: 50px;
    top: 20%;
    left: 70px;
    background-color: rgba(173, 216, 230, 0.5);
    position: absolute;
}

.circle-blue-2 {
    width: 180px;
    height: 180px;
    bottom: 10%;
    right: -80px;
    background-color: rgba(173, 216, 230, 0.5);
    position: absolute;
}

/* App Container (replaces container-fluid min-vh-100 d-flex align-items-center) */
.app-container {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    color: HSL(30, 10%, 15%);
}

/* Main Layout (replaces row w-100) */
.main-layout {
    display: grid;
    grid-template-columns: 2fr 1fr;
    /* Replaces col-md-8 and col-md-4 */
    width: 100%;
    max-width: 1200px;
    gap: 20px;
}

/* Welcome Section (replaces col-md-8 d-flex align-items-center justify-content-center) */
.welcome-section {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

/* Login Section (replaces col-md-4 d-flex align-items-center justify-content-center) */
.login-section {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

/* Welcome Section Styles */
.welcome-content {
    text-align: center;
    padding-left: 2rem;
    text-align: left;
}

.welcome-title {
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    color: #6cc8ff;
}


.highlight {
    /* color: #f5a623;  */
    color: #8e5b34;
}


.welcome-subtitle {
    color: #6c757d;
    font-size: 0.9rem;
    margin-bottom: 1.5rem;
}

.welcome-heading {
    font-size: 3rem;
    font-weight: 700;
    margin-bottom: 1rem;
}

.highlight-blue {
    /* color: #007bff;  */
    color: #8e5b34;
}

.welcome-description {
    /* color: #6c757d; */
    color: #70665c;
    font-size: 1.25rem;
    margin-bottom: 1.5rem;
}

.badge-container {
    display: flex;
    justify-content: start;
    gap: 1.5rem;
    margin-top: 1.5rem;
}

.badge-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: left;
    font-size: 0.85rem;
    /* Slightly smaller font size to match the image */
    font-weight: 500;
    color: #333;
}

.badge-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    /* Size of the circular icon background */
    height: 50px;
    border-radius: 50%;
    /* Circular shape */
    font-size: 1.5rem;
    /* Icon size */
    margin-bottom: 8px;
    /* Space between icon and text */
}

/* Specific background colors for each badge icon */
.badge-item:nth-child(1) .badge-icon {
    background-color: #f5e6d3;
    /* Light beige for "Fresh Ingredients" */
}

.badge-item:nth-child(2) .badge-icon {
    background-color: #e6f0fa;
    /* Light blue for "Custom Meals" */
}

/* Badge Text */
.badge-text {
    line-height: 1.2;
    /* Adjust line spacing for the two lines of text */
}

.badge-text:nth-child(1) {
    color: #007bff;
}

/* Login Form Styles */
.login-form-container {
    max-width: 400px;
    width: 100%;
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 1.5rem;
}

.tab-list {
    list-style: none;
    display: flex;
    border-bottom: 1px solid #dee2e6;
    margin-bottom: 1.5rem;
}

.tab-item {
    flex: 1;
}

.tab-link {
    display: block;
    text-align: center;
    padding: 10px;
    text-decoration: none;
    color: #6c757d;
    font-weight: 500;
}

.tab-link.active {
    /* color: #007bff; */
    color: #383431;
    /* color: #f1ae7e; */
    border-bottom: 2px solid #f1ae7e;
}

.form-group {
    margin-bottom: 1rem;
}

.input-wrapper {
    display: flex;
    align-items: center;
    border: 1px solid #ced4da;
    border-radius: 5px;
    overflow: hidden;
}

.input-icon {
    padding: 10px;
    /* background-color: #f8f9fa; */
    /* border-right: 1px solid #ced4da; */
}

.form-input {
    flex: 1;
    padding: 10px;
    border: none;
    outline: none;
    font-size: 1rem;
}

.forgot-password {
    text-align: right;
    margin-bottom: 1rem;
}

.forgot-link {
    color: #f7cdaf;
    font-size: 0.9rem;
    text-decoration: none;
}

.forgot-link:hover {
    /* text-decoration: underline; */
    text-decoration: none;
    border-bottom: 2.5px solid #f7cdaf;
}

.submit-button {
    color: #8e5b34;
    font-weight: 500;
    width: 100%;
    padding: 10px;
    background-color: hsla(25, 82%, 72%, 1);
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    margin-bottom: 1rem;
}

.submit-button:hover {
    background-color: RGB(247, 212, 186);
}

.form-footer {
    text-align: center;
    color: #6c757d;
    font-size: 0.9rem;
}

.form-link {
    /* color: #007bff; */
    color: #f7cdaf;
    text-decoration: none;
}

.form-link:hover {
    /* text-decoration: underline; */
    text-decoration: none;
    border-bottom: 2.5px solid #f7cdaf;
}

/* Responsive Adjustments */
@media (max-width: 767px) {
    .main-layout {
        grid-template-columns: 1fr;
        /* Stack sections vertically on small screens */
        gap: 0;
    }

    .welcome-content {
        text-align: center;
        padding-left: 0;
        text-align: left;
    }

    .welcome-title {
        font-size: 2rem;
    }

    .welcome-heading {
        font-size: 2.5rem;
    }

    .welcome-description {
        font-size: 1rem;
    }

    .badge-container {
        display: none;
        /* margin-bottom: 2rem; */
    }

    body::before {
        width: 60px;
        height: 60px;
        top: 80px;
        right: 40px;
    }

    body::after {
        width: 120px;
        height: 120px;
        bottom: -60px;
        left: -20px;
    }

    .circle-blue-1 {
        width: 30px;
        height: 30px;
        top: 10%;
        left: 20px;
    }

    .circle-blue-2 {
        width: 110px;
        height: 110px;
        bottom: 3%;
        right: -50px;
    }
}

@media (min-width: 768px) {
    .welcome-content {
        padding-left: 3rem;
        /* Shift content to the right on larger screens */
    }
}